.user-profile-title {
  text-align: center;
  margin: 40px 0 20px 0;
  font-size: 32px;
  font-weight: 700;
  color: #2c3e50;
  letter-spacing: 1.2px;
}

.user-profile-container {
  max-width: 600px;      /* —É–≤–µ–ª–∏—á–∏–ª–∏ —à–∏—Ä–∏–Ω—É */
  margin: 0 auto 60px auto;  /* —Ü–µ–Ω—Ç—Ä–∏—Ä—É–µ–º, —Å–Ω–∏–∑—É –æ—Ç—Å—Ç—É–ø */
  padding: 40px 35px;    /* —É–≤–µ–ª–∏—á–∏–ª–∏ –ø–∞–¥–¥–∏–Ω–≥ */
  background-color: #f7f9fc;
  border-radius: 14px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.12); /* —á—É—Ç—å —Å–∏–ª—å–Ω–µ–µ —Ç–µ–Ω—å */
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}

.user-profile-field {
  margin-bottom: 22px;    /* —á—É—Ç—å –±–æ–ª—å—à–µ –æ—Ç—Å—Ç—É–ø */
  font-size: 20px;        /* –±–æ–ª—å—à–µ —à—Ä–∏—Ñ—Ç */
  line-height: 1.5;
  border-bottom: 1px solid #ddd;
  padding-bottom: 10px;
}

.user-profile-label {
  font-weight: 700;
  color: #34495e;
  margin-right: 12px;
}

.user-profile-loading,
.user-profile-error {
  text-align: center;
  font-size: 20px;
  padding: 50px 0;
  color: #999;
}

.user-profile-error {
  color: #e74c3c;
}
