.cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin: 0 auto;
    max-width: 1200px;
    padding-top: 50px; /* –ö–æ–º–ø–µ–Ω—Å–∏—Ä—É–µ–º –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω—ã–π translateY */
    margin-bottom: 50px; /* –î–æ–±–∞–≤–ª—è–µ–º –º–µ—Å—Ç–æ —Å–Ω–∏–∑—É */
    overflow: visible; /* –†–∞–∑—Ä–µ—à–∞–µ–º –≤—ã—Ö–æ–¥ –∑–∞ –≥—Ä–∞–Ω–∏—Ü—ã */
  }
  
  .card {
    background: white;
    border-radius: 15px;
    border: solid 5px #570f80;
    padding: 25px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    position: relative; /* –î–ª—è –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–≥–æ z-index */
  }
  .card h1{
    font-size: 30px;
    font-weight: bold;
    color: #000000;
  }

  .card p{
    margin-top: 20px;
    font-size: 20px;
  }
  .zagolovok{
    font-size: 50px;
    font-weight: bold;
    color: #570f80;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .card:nth-child(odd) {
    transform: translateY(-50px);
    z-index: 1; /* –ü–æ–¥–Ω–∏–º–∞–µ–º –≤—ã—à–µ —Å–æ—Å–µ–¥–Ω–∏—Ö –∫–∞—Ä—Ç–æ—á–µ–∫ */
  }
  
  @media (max-width: 768px) {
    .cards {
      padding-top: 0; /* –ù–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö —É–±–∏—Ä–∞–µ–º –∫–æ–º–ø–µ–Ω—Å–∞—Ü–∏—é */
    }
    .card:nth-child(odd) {
      transform: none;
    }
  }