.kurs {
    position: relative;
    background-color: #6a0dad;
    padding: 30px;
    border: solid 3px #570f80;
    border-radius: 20px;
    text-align: left;
    color: #e4e3e3;
    font-size: 18px;
    width: 100%; /* –ó–∞–Ω–∏–º–∞–µ—Ç –≤—Å—é —à–∏—Ä–∏–Ω—É —è—á–µ–π–∫–∏ —Å–µ—Ç–∫–∏ */
    box-sizing: border-box; /* –£—á–∏—Ç—ã–≤–∞–µ—Ç padding –≤ –æ–±—â–µ–π —à–∏—Ä–∏–Ω–µ */
    min-height: 300px; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –≤—ã—Å–æ—Ç–∞ –¥–ª—è –æ–¥–∏–Ω–∞–∫–æ–≤—ã—Ö –∫–∞—Ä—Ç–æ—á–µ–∫ */
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* –†–∞—Å–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ –ø–æ –≤—ã—Å–æ—Ç–µ */
}

.kurs_grid {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    gap: 30px;
    padding: 20px;
    justify-content: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ —Å–µ—Ç–∫–∏ */
}

