.tabs {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  gap: 20px;
}

.tab-button {
  padding: 12px 24px;
  font-size: 18px;
  background-color: #e4e3e3;
  color: #570f80;
  border: none;
  border-radius: 24px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
}

.tab-button:hover {
  background-color: #d0c0e0;
}

.tab-button.active {
  background-color: #570f80;
  color: #e4e3e3;
}